@import url('https://fonts.googleapis.com/css2?family=Parisienne&family=Playfair+Display:ital,wght@0,400..900;1,400..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tangerine:wght@400;700&display=swap');


body,
#root {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: auto;
  width: auto;
}

.Header {
  background-color: #1f1a1a;
  height: 14vh;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 2%; /* evita encostar nas bordas */
  box-sizing: border-box;
}

/* Ajusta o título */
.Hsw {
  font-family: "Tangerine", cursive;
  font-weight: 400;
  font-style: italic;
  font-size: 2.5rem; /* use rem ou em para responsivo */
  white-space: nowrap; /* evita quebrar em telas pequenas */
}

/* Navigation flexível */
.Navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex: 1; /* ocupa espaço disponível */
}

.Navigation p {
  font-size: 1.2rem; /* menor em telas pequenas */
  margin: 0 0.5rem;
}

/* Logo */
.Logo img {
  width: auto;
  height: 80%;
}

.Body {
  background-color: #ffffff;
  height: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: center;
}

/* Seções */
.Home, .Vantagens, .Contato {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Footer */
.Footer {
  height: auto;
  padding: 2rem 0;
  font-size: 1.5rem;
}